.icon svg {
  width: 20px;
  height: 20px;
}

.App .process {
  cursor: pointer;
}
.App .process svg {
  color: #722ed1;
}
.App .complete svg {
  color: rgba(0, 0, 0, 0.25);
}
.App .wait {
  cursor: pointer;
}

.buttonWrapperStep {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  width: 100%;
}
.buttonStep {
  width: 100% !important;
}
.titleTile {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.stepName {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
